




































import Vue from "vue";
// import Types from '@/components/money/Types.vue';
import { Prop, Component } from "vue-property-decorator";
import Tabs from "@/components/Tabs.vue";
import intervalList from "@/constants/IntervalList.ts";
import typesList from "@/constants/Types";
import dayjs from "dayjs";
import clone from "@/lib/clone";

@Component({
  components: { Tabs }
})
export default class Statistics extends Vue {
  typeVal = "-";
  interval = "day";

  intervalList = intervalList;
  typesList = typesList;

  // 
  created() {
    this.$store.commit("fetchRecords");
  }
  // tags 
  tagString(tags: Tag[]) {
    // tags.toString() tags.join(',')
    // if(tags.length>0){
    //       let res:string[] = [];
    //   tags.map(item=>res.push(item.name));
    //   return res.join(',');
    // }else{
    //   return "";
    // }
    // .map  
    return tags.length === 0 ? "" : tags.map(item => item.name).join(",");
  }
  // recordList
  get recordList() {
    return (this.$store.state as RootStateType).recordList;
  }
  // dayjs 

  // string  ISO 
  beautify(value: string) {
    const day = dayjs(value);
    const now = dayjs();
    if (day.isSame(now, "day")) {
      return "";
    } else if (day.isSame(now.subtract(1, "day"), "day")) {
      return "";
    } else if (day.isSame(now.subtract(2, "day"), "day")) {
      return "";
    } else if (day.isSame(now, "year")) {
      //
      return day.format("MD");
    } else {
      return day.format("YYYYMMDD");
    }
  }
  //
  //
  // get result(){
  //   // 
  //   const {recordList} = this;
  //   type Items = RecordItem[];
  //   type HashTableValue = {title:string,items:Items};
  //   // hashTable hashTable  RecordItem;
  //   // . [],
  //   const hashTable:{[key:string]:HashTableValue}={};
  //   // const hashTable1:{[key:string]:RecordItem[]}={};
  //   for(let i =0;i<recordList.length;i++){
  //    const [date,time]= recordList[i].createAt!.split('T');
  //    //
  //   // hashTable[date] 
  //   // 
  //   // {[]}   {{title,[]}}
  //   hashTable[date] = hashTable[date] || {title:date,items:[]};
  //   hashTable[date].items.push(recordList[i]);
  //   }
  //   return hashTable;
  // }

  //  hashTable  key
  get res() {
    const { recordList } = this;
    // recordList return
    if (recordList.length === 0) {
      return;
    }
    type HashTableValue = { titile: string; items: RecordItem[] }[];
    // recordlist
    //  sortclone 
    // 
    const newList = clone(recordList)
      .filter(item => item.type === this.typeVal)
      .sort(
        (a, b) => dayjs(b.createAt).valueOf() - dayjs(a.createAt).valueOf()
      );
    if (newList.length === 0) {
      return;
    }
    type Result = { title: string; total?: number; items: RecordItem[] }[]; // ,total 
    // ,
    // recordList   [{title:'a',items:[]},{},{}]
    //  recordList 0, itemscreateAt date
    const resultArray: Result = [
      {
        title: dayjs(newList[0].createAt).format("YYYY-MM-DD"),
        items: [newList[0]]
      }
    ];
    //  recordList 
    for (let i = 1; i < newList.length; i++) {
      //
      const current = newList[i];
      // x
      const last = resultArray[resultArray.length - 1];
      if (dayjs(last.title).isSame(dayjs(current.createAt), "day")) {
        // resultArray 
        last.items.push(newList[i]);
      } else {
        //new ,push  resultArray 
        resultArray.push({
          title: dayjs(newList[i].createAt).format("YYYY-MM-DD"),
          items: [current]
        });
      }
    }
    //()
    resultArray.map(group => {
      group.total = group.items.reduce((sum, item) => sum + item.amount, 0);
    });
    return resultArray;
  }
}
