import Vue from 'vue';
import {Component} from 'vue-property-decorator';
@Component
export default class TagHelper extends Vue  {
    createTag(){
        const name = window.prompt('');
        console.log(name)
        if(name){
          // name 
          this.$store.commit('createTag',name)
        }
      }
}