import createId from '@/lib/createId';
//label.vue model 
//model model type
type LabelModel = {
    data: Label[];
    fetch: () => Label[];
    createLabel: (name: string) => 'success' | 'duplicated';  // success duplicated 
    save: () => void;
    update: (id: string,name: string) => "success"| 'not Fund' | 'duplicated';
    remove: (id: string) => boolean;
}
type Label={
    id: string;
    name: string;
}

const localStorageKeyName = 'labelList';
const model: LabelModel = {
    // ,iddataLabel id
    data:[], 
    fetch() {
        this.data = JSON.parse(window.localStorage.getItem(localStorageKeyName) || '[]');
        return this.data;
    },
    save() {
        window.localStorage.setItem(localStorageKeyName, JSON.stringify(this.data));
    },
    createLabel(value) {
        const id = createId().toString();
        const labels = this.data.map(item=>item.name);
        if (labels.indexOf(value) > -1) {
            return "duplicated";
        }
        this.data.push({id,name:value});  // id
        this.save();
        return 'success';
    },
    // 
    update(id,name){
        const id_list = this.data.map(item =>item.id);
        const names = this.data.map(item =>item.name);
        const idIndex= id_list.indexOf(id);
        if(idIndex>-1){
            // id idname name  
            //name
            if(names.indexOf(name)>-1){
                return 'duplicated';
            }else{
                // id 
                const item = this.data[idIndex];
                // item.id = item.name = name; // id  name 
                item.name = name;
                this.save();
                return 'success';
            }
        }else{
            return 'not Fund';
        }
    },
    //
    remove(id){
        let index =1;
        for(let i =0;i<this.data.length;i++){
            if(this.data[i].id === id){
                index =i;
                break;
            }
        }
        this.data.splice(index,1);
        this.save();
        return true;
    }
}

export default model;
