
















import Vue from "vue";
import { Component, Prop } from "vue-property-decorator";
import LabelModel from "@/models/labelModel";
import Icon from "@/components/Icon.vue";
import Notes from "@/components/money/Notes.vue";
import Button from "@/components/money/Button.vue";

@Component({
  components: { Icon, Notes,Button },
  // script getter setter
  // computed:{
  //   //tag
  //   tag(){
  //     return this.$store.state.currentTag
  //   }
  // }
})
export default class EditLabel extends Vue {
  // computed 
  // tag?: { id: string; name: string }; //
  //
  //  computed vue-class-component
  get tag(){
    return this.$store.state.currentTag
  }
  created() {
    //$route 
    // const id = this.$route.params.id;
    // 
    // LabelModel.fetch();
    // const labels = LabelModel.data;
    //  labels  id = id 
    // const item = labels.filter(item => item.id === id)[0];
    //
    //
    //( id )
    this.$store.commit('fetchTags');
    this.$store.commit('setCurrentTag',this.$route.params.id);
    if (!this.tag) {
     // this.$router.push('/404');
      this.$router.replace("/404");
    } 
  }
  //notes input  @update:value
  //  $emit 
  updateLabel(value: string) {
    //model
    if (this.tag) {
      // LabelModel.update(this.tag.id, value);
      // store 
      this.$store.commit('updateTag',{id:this.tag.id,name:value})
    }
  }
  //
  remove() {
    //  store 
    if (this.tag) {
      // if (LabelModel.remove(this.tag.id)) {
      //   this.$router.back();
      // } else {
      //   window.alert("");
      // }
      this.$store.commit('remove',this.tag.id);
    }
  }
  goback() {
    this.$router.back();
  }
}
