// svg 
const importAll = (requireContext: __WebpackModuleApi.RequireContext) => requireContext.keys().forEach(requireContext);

try{
  importAll(require.context('../assets/icon',true,/\.svg$/));
}catch(error){
  console.log(error)
}

export default {
  name: "Icon",
  props:['name'] // name
};
